import React from 'react'
import { ReactElement } from 'react'

import { SubjectSummary } from '../../../components/subject/SubjectSummary'
import { SubjectContent } from '../../../components/subject/SubjectContent'
import { SubjectTitle } from '../../../components/subject/SubjectTitle'
import { SubjectIdentifier } from '../../../components/subject/SubjectIdentifier'
import { Subject } from '../../../components/subject/Subject'

export function ActionRule () : ReactElement {
  return (
    <Subject>
      <SubjectIdentifier>rules-fight-action</SubjectIdentifier>
      <SubjectTitle>Actions</SubjectTitle>
      <SubjectSummary>

      </SubjectSummary>
      <SubjectContent>
        <p>
          <em>Action.</em> Quand une entitée prend l'action lors d'un tour,
          celle-ci doit alors décrire les décisions qu'elle souhaite prendre. Si
          le maître du jeu accepte la description proposée, l'entitée peut alors
          résoudre chaque décision en suivant les règles qui lui sont liée. Si
          la ou les décisions d'une entitée suivent un schéma pré-établi, il
          n'est pas utile de valider la décision.
        </p>

        <p>
          <em>Sous-charge.</em> Une entitée est considéré en sous-charge quand
          elle possède plus de points de charge qu'il n'en faut pour supporter
          l'entièreté de son équipement. Dans cette situation, tous les deux
          points de charge non utilisé sont convertis à la fois en points
          d'initiative bonus et en points de dégâts supplémentaires ajoutable
          aux attaques au corps à corps.
        </p>

        <p>
          <em>Sur-charge.</em> Tous les points de charge au dessus de la limite
          de l'entité implique la perte d'un point de dégât à toutes les
          attaques au corps à corps et la perte d'un point à son initiative.
        </p>
      </SubjectContent>
    </Subject>
  )
}
